import WrapLayout from '../layouts/wrapLayout.vue'
import TheSimpleNavBar from '../components/TheSimpleNavBar.vue'
import { toThousands } from '../utils/global.utils'

export default defineComponent({
  name: 'Top',
  components: {
    WrapLayout,
    TheSimpleNavBar
  },
  setup () {
    const article = computed(() => {
      return topData.article
    })
    const tableList = computed(() => {
      return topData.tableList
    })
    return {
      article,
      tableList,
      toThousands
    }
  }
})
