interface BaseInfo {
  type: string;
  length: number;
  title: string;
  tags: string[];
}
const baseInfoList = [
  {
    type: 'size',
    length: 13,
    title: '',
    tags: []
  },
  {
    type: 'price',
    length: 8,
    title: '',
    tags: []
  },
  {
    type: 'panel',
    length: 3,
    title: '',
    tags: ['OLED', 'QLED', 'LED']
  },
  {
    type: 'usage',
    length: 6,
    title: '',
    tags: ['PS 5', '4K', '', '', 'Xbox  X', '']
  }
]

function createSizeTags (tags: TagProps[], baseInfo: BaseInfo) {
  for (let i = 0, inc = 30; i < baseInfo.length; i++) {
    tags.push({
      id: i.toString(),
      text: (inc += 5).toString() + ''
    })
  }
  return tags
}

function createPriceTags (tags: TagProps[], baseInfo: BaseInfo) {
  for (let i = 0, inc = 1000; i < baseInfo.length; i++) {
    tags.push({
      id: i.toString(),
      text: '' + (inc += 1000)
    })
  }
  return tags
}

function createPanelUsageTags (tags: TagProps[], baseInfo: BaseInfo) {
  for (let i = 0; i < baseInfo.tags.length; i++) {
    tags.push({
      id: i.toString(),
      text: baseInfo.tags[i]
    })
  }
  return tags
}

const groupList = []
for (let i = 0; i < baseInfoList.length; i++) {
  const baseInfo = baseInfoList[i]
  let tags: TagProps[] = []
  switch (baseInfo.type) {
    case 'size':
      tags = createSizeTags(tags, baseInfo)
      break
    case 'price':
      tags = createPriceTags(tags, baseInfo)
      break
    default:
      tags = createPanelUsageTags(tags, baseInfo)
      break
  }
  groupList.push({
    id: i,
    title: baseInfo.title,
    tags: tags
  })
}

export const seekData = {
  groupList
}

