const dateArr = ['', '39 ', '6 ', '8 ', '15 ', '', '2 ', '2021/8/18', '2021/8/17', '2021/8/16', '2021/8/15', '2021/8/14', '2021/8/13', '2021/8/12', '2021/8/11', '2021/8/10', '2021/8/9', '2021/8/8']
export const shareData = {
  waterFall: (page: number, size: number, random: boolean) => {
    return [...Array(size).keys()].map((key) => {
      const prev = (page - 1) * size
      const banner = {
        // url: Random.image()
        url: random ? `/images/waterFall/${Random.natural(0, 20)}.webp` : `/images/waterFall/${key}.webp`
      }
      const avatar = {
        // url: Random.image('16x16')
        url: `/images/avatar/${Random.natural(0, 14)}.jpeg`
      }
      return {
        id: key + 1 + prev,
        key: Random.guid(),
        title: Random.csentence(10, 20),
        banner,
        captions: [Random.cparagraph()],
        author: Random.cname(),
        avatar,
        good: Random.integer(0, 300)
      }
    })
  },
  attention: (size: number) => {
    return [...Array(size).keys()].map((key) => {
      const avatar = {
        url: `/images/avatar/${Random.natural(0, 14)}.jpeg`
      }
      const banner = {
        url: `/images/waterFall/${Random.natural(0, 20)}.webp`
      }
      return {
        id: key + 1,
        key: Random.guid(),
        avatar,
        author: Random.cname(),
        authorIsV: Random.boolean(1, 5, true),
        moment: dateArr[Random.natural(0, 17)],
        banner,
        captions: [Random.cparagraph(3, 5), Random.cparagraph(0, 2)],
        comment: Random.integer(0, 50),
        good: Random.integer(0, 500)
      }
    })
  }
}

