<template>
  <van-tabbar v-show="isShow" v-model="activeName" class="tabbar">
    <van-tabbar-item :name="routeNamesEnum.Home" :to="homeTabPath" icon="wap-home">
      <span></span>
      <template #icon="props">
        <img :src="props.active ? homeIcon.active : homeIcon.inactive" alt="">
      </template>
    </van-tabbar-item>
    <van-tabbar-item :name="routeNamesEnum.Share" :to="shareTabPath" icon="smile-comment">
      <span></span>
      <template #icon="props">
        <img :src="props.active ? compassIcon.active : compassIcon.inactive" alt="">
      </template>
    </van-tabbar-item>
  </van-tabbar>
</template>

<script lang="ts">
import { computed, defineComponent, ref, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { routeNamesEnum } from '../router/routerEnum'
import { useStore } from 'vuex'
import { StoreType, storeKey } from '../store/store'
import useGetTabbarActiveNameForPath from '../hooks/useGetActiveRoute'
import { Tabbar, TabbarItem } from 'vant'

export default defineComponent({
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  },
  setup () {
    const store = useStore<StoreType>(storeKey)
    const route = useRoute()
    const activeName = ref(useGetTabbarActiveNameForPath(route.fullPath))

    const homeIcon = {
      active: require('../assets/home-active.svg'),
      inactive: require('../assets/home.svg')
    }
    const compassIcon = {
      active: require('../assets/compass-active.svg'),
      inactive: require('../assets/compass.svg')
    }
    return {
      activeName,
      homeIcon,
      compassIcon,
      routeNamesEnum,
      homeTabPath: computed(() => store.state.theTabbarComponent.homePath),
      shareTabPath: computed(() => store.state.theTabbarComponent.sharePath),
      /* TODO:    */
      isShow: computed(() => store.state.theTabbarComponent.isShow)
    }
  }
})
</script>

<style lang="stylus" scoped>
.tabbar
  --van-tabbar-item-active-color var(--ot-a-active-color)
:root
  // tabbar
  --van-tabbar-background-color #18181c

</style>
