import { defineComponent, provide } from 'vue'
import { deviceWidthKey, globalStyleKey } from './symbols/global'
import { GlobalStyleType } from './types/global'
import { useWindowSize } from '@vant/use'

/* TODO: props property  default  aa && aa.bb  aa || bb  aa ? aa.aa : 'abc' */

export default defineComponent({
  name: 'App',
  components: {
  },
  setup () {
    const { width } = useWindowSize()
    // console.log(width, window.innerWidth)
    //    
    const deviceWidth: number = width.value
    const globalStyle: GlobalStyleType = { rowPadding: 16 }
    provide(deviceWidthKey, deviceWidth)
    provide(globalStyleKey, globalStyle)
  }
})
